.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

._loading-overlay{
	height:85%;
}

._loading-overlay > .eYbJzY{
	background-color:rgba(0,0,0,0);
}
#messages{
	height:100%;
	overflow-y:scroll;
}

.message{
	/*border-radius:25px;
	padding:10px;
	width: 100%;
	margin-bottom:5px;
	float:right;*/
	margin-bottom:5px;
	width:100%;
	border-style:solid;
	border-top-width:0px;
	border-left-width:0px;
	border-right-width:0px;
	border-bottom-width: 1px;
	word-break:break-all;
	color:#DFDCE3;
}
#title{
	display:table-cell;
	vertical-align:middle;
}
#time{
	font-size:30px;
}

#header{
	width:100%;
	display:table;
	height:10%;
	font-size:30px;
}

.author{
	/*text-decoration:underline;*/
	color:#dfdce3;
}

/*.my-message{
	float:left;
	background:lightcoral;
}*/

html,body, #root, #root>div, #pageDiv{
	height:100%;
	overflow-y:hidden;
	overflow-x:hidden;
}

#footerDiv{
	height:4%;
}

#footerDiv>div{
	display:inline;
	float:right;
}

#roomWrapper{
	height:90%;
}

.bodyDiv{
	height:96%
}


